.switch {
  position: relative;

  input {
    display: none;

    & + label {
      border-radius: 9px;
      // padding: 16px 0 16px 20px;
      // min-width: 208px;
      margin-right: 25px;
      display: block;
      cursor: pointer;
      position: relative;

      .switch__track {
        position: relative;
        display: inline-block;
        z-index: 1;
        vertical-align: top;
        height: 22px;
        width: 40px;
        border-radius: 11px;
        background: #ECEFFC;

      .switch__dot {
        position: absolute;
        width: 24px;
        height: 24px;
        border-radius: 50%;
        left: -1px;
        top: -1px;
        transform: translateX(var(--offset, 0));
        transition: transform .4s;

        &:before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          right: 0;
          bottom: 0;
          border-radius: inherit;
          background: linear-gradient(222deg, #072B9D 25.94%, #7000FF 65.68%);
          transition: opacity .4s;
        }
      }
    }
  }

    &:checked {
      & + label {
        --offset: 18px;
      }
    }
  }
}
